.Modal {
    display: none;
}
.ModalActive{
    width: 100%;
    height: 100vh;
    background:rgba(0, 0, 0, 0.3);
    position:fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.5s;
    border: 1px solid #5d7cb8;
    z-index: 99999;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px;
    color: #848DAF;

}
.ModalContent{
    transition: 0.5s all;

}
.ModalContentActive{
    margin-top: -264px;
    width: 643px;
    height: 507px;
    background:  #FFFFFF;
    border-radius: 6px; 
}
.ModalOff{
    width: 10px;
    height: 10px;
}
.ModalOffLink{
    position: absolute;
    width: 32px;
    height: 32px;
    opacity:1;
}

.ModalOffLink:hover {
    opacity: 0.8;
    cursor: pointer;
}
.ModalOffLink:hover:after, .ModalOffLink:hover:before {
    background:#ED3135;

}
.ModalOffLink:before, .ModalOffLink:after {
    position: absolute;
    top: 18px;
    left: 616px;
    content: ' ';
    height: 15px;
    width: 2px;
    background:  #6C78B0;

}
.ModalOffLink:before {
    transform: rotate(45deg);
}
.ModalOffLink:after {
    transform: rotate(-45deg);
}
.ModalDeliveryName{   
    font-size: 20px;
    line-height: 27px;
    color: #000000;
    margin: 28px auto 0 auto;
    width: 543px;
    height: 50px;
    border-bottom:1px solid rgb(174, 182, 211, 0.3);
}

.ModalDeliveryItem{
    margin: 0 auto;
    width: 583px;
    border: 1px solid #FFFFFF;
    border-radius: 3px;
    padding-top: 24px;
   
}
.ModalDeliveryItem:hover{
    border: 1px solid #7072f3;
}

.ModalDeliveryOne{
    margin:0 0 0 20px;
    width: 513px;
    height: 46px;
    border-bottom:1px solid rgb(174, 182, 211, 0.3);
    display: flex;
}
.ModalDeliveryRadio {
    position: absolute;
    z-index: -1;
    opacity: 0;
}

.ModalDeliveryRadio+label {
    display: inline-flex;
    align-items: center;
    user-select: none;
    cursor: pointer;
}

.ModalDeliveryRadio+label::before {
    content: '';
    display: inline-block;
    width: 23px;
    height: 23px;
    flex-shrink: 0;
    flex-grow: 0;
    border: 1px solid #ADB6D8;
    border-radius: 50%;
    margin-right: 9px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50% 50%;
}

.ModalDeliveryRadio:not(:disabled):not(:checked)+label:hover::before {
    border-color: #b3d7ff;
}

.ModalDeliveryRadio:not(:disabled):active+label::before {
    background-color: #b3d7ff;
    border-color: #b3d7ff;
}

.ModalDeliveryRadio:focus:not(:checked)+label::before {
    border-color: #80bdff;
}

.ModalDeliveryRadio:checked+label::before {
    color: #6739ff;
    background-color: #243DA1;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='%23fff'/%3e%3c/svg%3e");
}
.ModalDeliveryRadio:checked+label {
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 19px;
    color: #000000;
}
.OneImg{
    width: 63px;
    height: 23px;
    margin: 0 16px 0 6px;
}
.OneImg img{
    width: 100%;
    object-fit: cover;	
    height: auto;
}


.OneName{
    width: 120px;
    height: 23px;
    opacity: 0.5;
    font-size: 14px;
    line-height: 19px;
    color: #000000;
}
.OneData{
    width: 170px;
    height: 23px;
    font-size: 14px;
    line-height: 19px;
    color: #000000;
    margin: 0px 0px 0px 50px;
}
.OnePrice{
    width: 52px;
    height: 23px;
    font-weight: bold;
    font-size: 16px;
    line-height: 22px;
    color: #000000;
}
@media screen and (max-width: 720px) {
    .ModalOffLink:before, .ModalOffLink:after {
        top: 9px;
        left: 282px;  
    }
    .ModalContentActive{
       margin-top: 20px;
        width: 300px;
       height:396px;
    }
    .ModalDeliveryOne{
        margin:0 auto 0 auto;
        width: 250px;
        height: 46px;
        flex-wrap: wrap;
    }
    .ModalDeliveryName{   
        margin: 6px auto 0px auto;
        padding: 0px 0 12px 0;
        width: 253px;
        font-size: 15px;
        line-height: 20px;
    }
    .ModalDeliveryRadio+label::before {
        width: 21px;
        height: 21px;
        margin-left: -2px;
    }
    .OneImg{
        width: 45px;
        margin: 0 45px 0 -5px;
        order: 2;

    }
    .OneName{
        padding-left: 22px;
        width: 96px;
        height: 19px;
        font-size: 12px;
        line-height: 16px;
        
        order: 4;
    }
    .OneData{
        width: 118px;
        height: 19px;
        font-size: 12px;
        line-height: 16px;
        margin: 0;
        order: 3;
    }
    .OnePrice{
        width: 52px;
        height: 19px;
        font-size: 14px;
        line-height: 19px;
        margin-top: -8px;
        order: 5;
    }
    .ModalDeliveryItem{
       width: 253px;
        border: 1px solid #FFFFFF;
        padding: 7px 0 4px 0;
       
    }



}